import React from 'react'
import { Element } from 'react-scroll'
import './Skill.css'
import image from './th.jpg'
import { LinearProgress } from '@material-ui/core'
const Skill = () => {
    return (
        <Element className='skillname' id='skill'>
            <div className='skillimage'>
                <img src={image} alt="" />
            </div>
            <div className='skilltext'>
                <h2>SKILLSET</h2>

                <div className='skillsets'>
                    <h5>HTML</h5>
                    <div className="skill_silder skill_silder1">
                        <LinearProgress variant="determinate" value={100} />
                    </div>
                </div>
                <div className='skillsets'>
                    <h5>css</h5>
                    <div className="skill_silder skill_silder2">
                        <LinearProgress variant="determinate" value={87} />
                    </div>
                </div>
                <div className='skillsets'>
                    <h5>boostrap</h5>
                    <div className="skill_silder skill_silder3">
                        <LinearProgress variant="determinate" value={70} />
                    </div>
                </div>
                <div className='skillsets'>
                    <h5>javascipt</h5>
                    <div className="skill_silder skill_silder4">
                        <LinearProgress variant="determinate" value={60} />
                    </div>
                </div>
                <div className='skillsets'>
                    <h5>React</h5>
                    <div className="skill_silder skill_silder5">
                        <LinearProgress variant="determinate" value={50} />
                    </div>
                </div>
            </div>
        </Element>
    )
}

export default Skill