import React from 'react'
import { Link } from "react-scroll"
import "./content.css"

const Content = () => {
  return (
    <div className='content'>
      <div className='container'>
        <h1>Srikanth Bobby</h1>
        <p>Learn web development With React js</p>
        <a href="www.google.com"><button className='button_left'>Download Cv2</button></a>
        <Link to='project' scroll={true} duration={500}>
          <button className='button_right'>My Work</button>
        </Link>
      </div>
    </div>
  )
}

export default Content
