import React, { useState } from 'react';
import "./Project.css"

const Project = ({ img, title, des, link }) => {
    const [show, setShow] = useState(false);
    return (
        <a href={link}>
            <div
                className='project'
                onMouseEnter={() => setShow(true)}
                onMouseLeave={() => setShow(false)}
            >
                {show ? (
                    <div className='pro'>
                        <h3>{title}</h3>
                        <p>{des}</p>
                    </div>
                ) : (
                    <img src={img} alt="" />
                )}
            </div>
        </a>
    )
};

export default Project;