import React from 'react'
import { Element } from 'react-scroll'
import { IconButton } from '@material-ui/core'
import { Facebook, Instagram, LinkedIn } from "@material-ui/icons"
import "./Contact.css"

const Contact = () => {
    return (
        <Element className='contact'>
            <h1>Contact</h1>
            <div className='p'>
                <p>
                    Email: <span>srisrikanth2004@gmail.com</span>
                </p>
                <p>
                    Number: <span>9095912128</span>
                </p>
                <div className='icons'>
                    <a href="www.google.com">
                        <IconButton>
                            <Facebook />
                        </IconButton>
                    </a>
                    <a href="www.google.com">
                        <IconButton>
                            <Instagram />
                        </IconButton>
                    </a>
                    <a href="www.google.com">
                        <IconButton>
                            <LinkedIn />
                        </IconButton>
                    </a>
                </div>
            </div>
        </Element>
    )
}

export default Contact
