import React from 'react'
import "./Experiment.css"
import { Element } from 'react-scroll'
import Box from '../Box/Box'

const Experiment = () => {
    return (
        <Element className="ex">
        <h1>Experiment</h1>
        <div className='exper'>
        <Box number='+10' title='Client'/>
        <Box number='60+' title='projects' style={{background:"orangered"}}/>
        <Box number='6+' title='Years Traning'/>
        <Box number='60000+' title='Student got training'/>
        </div>
        </Element>
    )
}

export default Experiment